* {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif; }

.UsersPhoto {
  width: 100px !important;
  height: 100px !important; }

.app-wrapper {
  display: grid;
  width: 80%;
  height: 90vh;
  margin: 0 auto;
  grid-template-columns: 2fr 10fr;
  grid-template-rows: 60px 1fr;
  grid-gap: 10px;
  grid-template-areas: 'header header' 'nav content'; }

header {
  grid-area: header;
  background-color: greenyellow; }
  header img {
    height: 100%; }

.loginBlock {
  float: right; }
  .loginBlock a {
    margin: 0;
    color: black;
    text-decoration: none; }

nav {
  grid-area: nav;
  background-color: green;
  padding: 15px; }
  nav a {
    text-decoration: none;
    color: white;
    font-size: 20px; }
  nav a.active {
    color: gold; }
  nav .friends {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-column-gap: 10px; }
    nav .friends a {
      color: white;
      grid-column: 1/5; }
    nav .friends div h4 {
      text-align: center;
      margin: 0; }
    nav .friends div img {
      width: 100%; }

.contentUsers {
  grid-area: content; }

.content, .Users {
  background-color: green; }
  .content img, .Users img {
    width: 100%;
    height: 200px; }
  .content .avatar, .Users .avatar {
    display: grid;
    width: 100%;
    height: auto;
    grid-gap: 10px;
    grid-template-columns: auto 1fr; }
    .content .avatar img, .Users .avatar img {
      width: auto; }
  .content .item img, .Users .item img {
    border-radius: 100%;
    width: 50px;
    height: auto; }
  .content .MyPosts, .content .ProfileInfo .avatar, .Users .MyPosts, .Users .ProfileInfo .avatar {
    padding: 10px; }

.spiner {
  grid-column: 2;
  grid-row: 2/3;
  z-index: 3; }

.SelectedPage {
  font-weight: bold; }

.dialogs {
  display: grid;
  grid-template-columns: 1fr 5fr; }
  .dialogs .dialogs-items {
    padding: 10px;
    background-color: #004242; }
    .dialogs .dialogs-items .dialog a {
      text-decoration: none;
      color: white; }
    .dialogs .dialogs-items .dialog .active {
      color: gold; }
  .dialogs .messages {
    background-color: azure;
    padding: 10px; }
    .dialogs .messages .enterText {
      display: grid;
      grid-template-columns: 50% 50%;
      align-items: center;
      grid-gap: 20px; }
      .dialogs .messages .enterText textarea {
        grid-column: 1; }
      .dialogs .messages .enterText button {
        align-self: baseline;
        height: 50px;
        width: 25%;
        grid-column: 2; }
